.maincontainer{
    width: 100%;
    background:rgb(32, 28, 32);
    height: 100vh;
    display: flex;
    justify-content: space-around;
}
.formdiv{
    width: 60%;
    margin: auto;
}
.resultdiv{
    width: 40%;
    margin: auto;
    padding-right: 30px;
}
@media (max-width: 670px){
    .maincontainer{
        display: flex;
        flex-flow: column;
        justify-content: center;
    }
    .formdiv{
        width: 100%;
        margin: 0 auto;
    }
    .resultdiv{
        width: 100%;
        padding-right: 0;
        margin-top: 0;
    }
}