.step1{
  height: 100%;
  width:60%;
  margin: auto;
  margin-top: 10px;
  padding: 10px;
  background: rgb(43, 59, 59);
  border-radius: 10px;
  border: rgb(153, 108, 100) solid;
}

.step1 input{
  background: transparent;
  border:1px tomato solid;
  border-radius: 8px;
  display: flex;
  flex-direction: column;
  width: 80%;
  margin: auto;
  margin-bottom: 5px;
  color: tan;
  text-align: center;
}  

.step1 input[type=number]::-webkit-inner-spin-button, 
input[type=number]::-webkit-outer-spin-button { 
-webkit-appearance: none; 
margin: 0; 
}

.step1 input:focus { 
  outline: none !important;
}
.step1 label{
  color: tan;
  font-family: sans-serif;
  width: 100%;
  text-align: center;
  font-size: medium;
}
.btndiv{
  width: fit-content;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
}
.btndiv button{
  height: 35px;
  width: 80px;
  border-radius: 20px;
  color: black;
  background: tomato;
  border: none;
}
button:focus{
  outline: none !important;
}

button:hover{
  color: tan;
}